





















import { Component, Vue, Prop, Watch } from 'vue-property-decorator'

@Component({
  components: {},
})
export default class Chip extends Vue {
  @Prop() text!: string
  @Prop({ default: 'bg-gray-100' }) bgColor!: string
  @Prop({ default: 'hover:bg-dark hover:text-white' }) hover!: string
}
